@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
.mode-toggler {
	font-weight: 600;
	font-size: 12px;
	color: var(--text-color-light);
	display: flex;
	align-items: center;
	background: none;
	border: none;
	outline: none;
	cursor: pointer;
}

.mode-toggler__input {
	display: none;
}

.mode-toggler__icon {
	font-size: 20px !important;
	margin-right: 5px;
}

@media (min-width: 992px) {
	.mode-toggler {
		font-size: 16px;
	}
}

/* DARK MODE */
.mode-toggler--dark.mode-toggler {
	color: var(--text-color-dark);
}

.header {
	width: 100%;
	height: 80px;
	background-color: #fff;
	box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.05);
}

.header__content {
	width: 100%;
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.header__logo {
	font-weight: 800;
	font-size: 14px;
	color: var(--text-color-light);
}

@media (min-width: 992px) {
	.header__logo {
		font-size: 24px;
	}
}

/* DARK MODE */
.header--dark.header {
	background-color: #2b3844;
}

.header--dark .header__logo {
	color: var(--text-color-dark);
}

.search {
	width: 100%;
	max-width: 480px;
	height: 48px;
	background-color: #fff;
	box-shadow: 0px 2px 9px rgba(0, 0, 0, 0.05);
	border-radius: 5px;
	position: relative;
}

.search__input {
	width: 100%;
	height: 100%;
	padding-left: 70px;
	border: none;
	outline: none;
	background: none;
	font-size: 12px;
}

.search__icon {
	color: #b2b2b2;
	position: absolute;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
}

.search__icon--search {
	left: 30px;
}

.search__icon--clear {
	cursor: pointer;
	right: 20px;
}

@media (min-width: 992px) {
	.search__input {
		font-size: 14px;
	}
}

/* DARK MODE */
.search--dark.search {
	background-color: #2b3844;
}

.search--dark .search__input::-webkit-input-placeholder {
	color: #fff;
}

.search--dark .search__input:-ms-input-placeholder {
	color: #fff;
}

.search--dark .search__input,
.search--dark .search__input::placeholder,
.search--dark .search__icon {
	color: #fff;
}

.filter {
	cursor: pointer;
	width: 200px;
	height: 48px;
	background-color: #fff;
	box-shadow: 0px 2px 9px rgba(0, 0, 0, 0.05);
	border-radius: 5px;
	font-size: 12px;
	color: var(--text-color-light);
	position: relative;
}

.filter__toggler {
	width: 100%;
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding-left: 24px;
	padding-right: 20px;
}

.filter__icon {
	font-size: 15px !important;
}

.filter__menu {
	position: absolute;
	top: 110%;
	left: 0;
	width: 100%;
	z-index: 10;
	background-color: inherit;
	box-shadow: 0px 2px 9px rgba(0, 0, 0, 0.05);
	border-radius: 5px;
	overflow: hidden;
	display: none;
}

.filter__menu--open {
	display: flex;
	flex-direction: column;
}

.filter__region {
	padding: 10px 24px;
}

.filter__region:hover {
	cursor: pointer;
	background-color: #ddd;
}

@media (min-width: 992px) {
	.filter {
		font-size: 14px;
	}
}

/* DARK MODE */
.filter--dark.filter {
	color: var(--text-color-dark);
	background-color: #2b3844;
}

.filter--dark .filter__region:hover {
	background-color: #29445e;
}

.controls {
	display: flex;
	flex-direction: column;
	grid-gap: 40px;
	gap: 40px;
	margin-top: 25px;
	margin-bottom: 30px;
}

@media (min-width: 992px) {
	.controls {
		flex-direction: row;
		align-items: center;
		justify-content: space-between;
		grid-gap: 0;
		gap: 0;
		margin-top: 50px;
		margin-bottom: 50px;
	}
}

.shimmer {
	width: 100%;
	height: 100%;
	background: none;
	overflow: hidden;
	-webkit-animation: loading 1.5s linear infinite;
	        animation: loading 1.5s linear infinite;
}

.shimmer__shine {
	width: 50%;
	height: 100%;
	-webkit-transform: translateX(50px) skewX(-10deg);
	        transform: translateX(50px) skewX(-10deg);
	background-color: rgba(255, 255, 255, 0.2);
	-webkit-filter: drop-shadow(0 0 20px rgba(255, 255, 255, 0.3));
	        filter: drop-shadow(0 0 20px rgba(255, 255, 255, 0.3));
}

@-webkit-keyframes loading {
	0% {
		-webkit-transform: translateX(-150%);
		        transform: translateX(-150%);
	}

	50% {
		-webkit-transform: translateX(0%);
		        transform: translateX(0%);
	}

	100% {
		-webkit-transform: translateX(150%);
		        transform: translateX(150%);
	}
}

@keyframes loading {
	0% {
		-webkit-transform: translateX(-150%);
		        transform: translateX(-150%);
	}

	50% {
		-webkit-transform: translateX(0%);
		        transform: translateX(0%);
	}

	100% {
		-webkit-transform: translateX(150%);
		        transform: translateX(150%);
	}
}

/* DARK MODE */
.shimmer--dark .shimmer__shine {
	background-color: rgba(255, 255, 255, 0.02);
}

.card-skeleton {
	width: 100%;
	height: 336px;
	margin: 0 auto;
	background: #fff;
	box-shadow: 0px 0px 7px 2px rgba(0, 0, 0, 0.03);
	border-radius: 5px;
	overflow: hidden;
	position: relative;
}

.card-skeleton__shimmer {
	position: absolute;
	top: 0;
	left: 0;
}

.card-skeleton__flag {
	width: 100%;
	height: 160px;
	background-color: #ddd;
}

.card-skeleton__info {
	padding: 24px;
}

.card-skeleton__title,
.card-skeleton__stat {
	border-radius: 5px;
}

.card-skeleton__title {
	width: 150px;
	height: 25px;
	background-color: #ddd;
	margin-bottom: 16px;
}

.card-skeleton__stat {
	width: 130px;
	height: 20px;
	background-color: #ddd;
	margin-bottom: 8px;
}

/* DARK MODE */
.card-skeleton--dark.card-skeleton {
	background-color: #2b3844;
}

.card-skeleton--dark .card-skeleton__flag,
.card-skeleton--dark .card-skeleton__title,
.card-skeleton--dark .card-skeleton__stat {
	background-color: #29445e;
}

.card {
	width: 100%;
	max-width: 500px;
	height: 336px;
	margin: 0 auto;
	background: #fff;
	box-shadow: 0px 0px 7px 2px rgba(0, 0, 0, 0.03);
	border-radius: 5px;
	overflow: hidden;
	transition: -webkit-transform 0.1s linear;
	transition: transform 0.1s linear;
	transition: transform 0.1s linear, -webkit-transform 0.1s linear;
}

.card:hover {
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
}

.card__flag {
	width: 100%;
	height: 160px;
	object-fit: cover;
}

.card__info {
	color: var(--text-color-light);
	padding: 24px;
}

.card__title {
	font-weight: 800;
	font-size: 18px;
	margin-bottom: 16px;
}

.card__stat {
	font-weight: 600;
	font-size: 14px;
	margin-bottom: 8px;
}

.card__stat--light {
	font-weight: 300;
}

/* DARK MODE */
.card--dark.card {
	background-color: #2b3844;
}

.card--dark .card__info {
	color: var(--text-color-dark);
}

.card-list {
	display: grid;
	grid-template-columns: 1fr;
	grid-gap: 40px;
	gap: 40px;
	margin-bottom: 20px;
}

@media (min-width: 768px) {
	.card-list {
		grid-template-columns: repeat(2, 1fr);
	}
}

@media (min-width: 992px) {
	.card-list {
		grid-template-columns: repeat(3, 1fr);
	}
}

@media (min-width: 1200px) {
	.card-list {
		grid-template-columns: repeat(4, 1fr);
		grid-gap: 75px;
		gap: 75px;
	}
}

.error {
	font-size: 20px;
	margin-top: 140px;
	color: var(--text-color-light);
	text-align: center;
}

/* DARK MODE */
.error--dark.error {
	color: var(--text-color-dark);
}

.scroll-to-top {
	width: 50px;
	height: 50px;
	border: none;
	outline: none;
	border-radius: 50%;
	background-color: #fff;
	box-shadow: 0px 0px 7px rgba(0, 0, 0, 0.3);
	visibility: hidden;
	opacity: 0;
	transition: all 0.2s linear;
}

.scroll-to-top--visible {
	visibility: visible;
	opacity: 1;
}

/* DARK MODE */
.scroll-to-top--dark {
	color: #fff;
	background-color: #2b3844;
}

.home {
	position: relative;
}

.home__scroll {
	cursor: pointer;
	position: fixed;
	right: 10px;
	bottom: 10px;
}

@media (min-width: 1200px) {
	.home__scroll {
		right: 20px;
		bottom: 20px;
	}
}


.loading {
	color: var(--text-color-light);
	font-size: 20px;
	width: 100%;
	height: calc(100vh - 80px);
	display: flex;
	align-items: center;
	justify-content: center;
}

/* DARK MODE */
.loading--dark {
	color: var(--text-color-dark);
}

.country-details {
	color: var(--text-color-light);
	width: 100%;
	height: 100%;
}

.country-details__link {
	margin-top: 40px;
	margin-bottom: 65px;
	display: flex;
	align-items: center;
	justify-content: center;
	font-weight: 300;
	font-size: 14px;
	color: var(--text-color-light);
	background-color: #fff;
	box-shadow: 0px 0px 7px rgba(0, 0, 0, 0.3);
	border-radius: 2px;
	width: 105px;
	height: 32px;
}

.country-details__icon {
	margin-right: 5px;
}

.country-details__content {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	grid-gap: 40px;
	gap: 40px;
}

.country-details__flag {
	width: 100%;
	height: 229px;
	object-fit: cover;
	border-radius: 5px;
}

.country-details__info {
	display: flex;
	flex-direction: column;
	grid-gap: 30px;
	gap: 30px;
}

.country-details__title {
	font-weight: 800;
	font-size: 22px;
}

.country-details__info-group {
	display: flex;
	flex-direction: column;
	grid-gap: 8px;
	gap: 8px;
}

.country-details__info-group--three {
	margin-bottom: 20px;
}

.country-details__stat {
	font-weight: 600;
	font-size: 14px;
}

.country-details__stat--light {
	font-weight: 300;
}

.country-details__borders {
	display: flex;
	flex-wrap: wrap;
	grid-gap: 10px;
	gap: 10px;
}

.country-details__border {
	color: #111517;
	background-color: #fff;
	box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.1);
	border-radius: 2px;
	font-weight: 300;
	font-size: 10px;
	padding: 6px 20px;
}

@media (min-width: 992px) {
	.country-details__content {
		flex-direction: row;
		grid-gap: 140px;
		gap: 140px;
	}

	.country-details__flag {
		width: 500px;
		height: 350px;
	}

	.country-details__info {
		flex-direction: row;
		flex-wrap: wrap;
	}

	.country-details__title {
		flex: 0 0 100%;
		font-size: 32px;
	}

	.country-details__info-group--one {
		flex: 0.5 1;
	}

	.country-details__info-group--two {
		flex: 0.5 1;
	}

	.country-details__info-group--three {
		margin-bottom: 0;
		flex: 0 0 100%;
	}

	.country-details__stat {
		font-size: 16px;
	}

	.country-details__border {
		font-size: 14px;
	}
}

/* DARK MODE */
.country-details--dark.country-details {
	color: var(--text-color-dark);
}

.country-details--dark .country-details__link,
.country-details--dark .country-details__border {
	color: var(--text-color-dark);
	background-color: #2b3844;
}

.not-found {
	width: 100%;
	height: calc(100vh - 80px);
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	grid-gap: 15px;
	gap: 15px;
}

.not-found__message {
	font-weight: 600;
	font-size: 14px;
	color: var(--text-color-light);
}

.not-found__path {
	color: #ef4b4c;
	font-weight: 800;
}

.not-found__button {
	cursor: pointer;
	font-size: 14px;
	font-weight: 300;
	padding: 5px 10px;
	border-radius: 2px;
	border: none;
	background-color: #fff;
	box-shadow: 0px 0px 7px rgb(0 0 0 / 29%);
}

@media (min-width: 992px) {
	.not-found {
		grid-gap: 25px;
		gap: 25px;
	}

	.not-found__message {
		font-size: 20px;
	}

	.not-found__button {
		font-size: 16px;
	}
}

/* DARK MODE */
.not-found--dark .not-found__message {
	color: var(--text-color-dark);
}

.app {
	width: 100%;
	min-height: 100vh;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	background-color: #f2f2f2;
}

/* DARK MODE */
.app--dark {
	background-color: #202c36;
}

:root {
	--text-color-light: #111517;
	--text-color-dark: #fff;
}

*,
*::before,
*::after {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: "Nunito Sans", sans-serif;
}

html {
	overflow-y: scroll;
}

ul {
	list-style: none;
}

a {
	text-decoration: none;
}

.container {
	max-width: 1280px;
	margin-left: auto;
	margin-right: auto;
	padding: 0 20px;
}

